import React, { Component } from 'react';
import { Row, Col, Input } from 'antd';
import '../static/css/app.css'
class login extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }
    render() {
        return (
            <Row type="flex" justify="center" align="middle" className="login">
                <Col xs={20} sm={16} md={13} lg={10} xl={8}>

                    <div className="login-box">
                        <div className="login-input">
                            <div>
                                LOGIN
                            </div>
                            <Input placeholder="input with clear icon" allowClear>123</Input>
                            <Input.Password placeholder="input password" />
                            <a href="#">忘记密码？</a>
                        </div>
                        <div className="login-btn">
                            <button className="logon-btns">
                                注册
                            </button>
                            <button className="login-btns">
                                登录
                            </button>
                        </div>
                    </div>
                </Col>
            </Row>
        );
    }
}

export default login;